import { Lounge } from './../../models/lounge.model';
import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Ticket } from 'src/models/ticket.model';
import { TicketShareForm } from 'src/models/ticket-share-form.model';

@Injectable({
  providedIn: 'root'
})
export class TicketsService {
  constructor(private httpClient: HttpClient) {}

  get baseUrl(): string {
    return `${environment.url}/tickets`;
  }

  reserveTicket(object: any): Observable<Lounge> {
    return this.httpClient.post<Lounge>(`${this.baseUrl}`, object);
  }

  getTickets(): Observable<Ticket[]> {
    return this.httpClient.get<Ticket[]>(this.baseUrl);
  }

  getTicket(id: number): Observable<Ticket> {
    return this.httpClient.get<Ticket>(`${this.baseUrl}/${id}`);
  }

  cancelTicket(id: number): Observable<any> {
    return this.httpClient.put<any>(`${this.baseUrl}/${id}/cancel`, {
      cancellationReason: 'TRAVEL_CANCELLED'
    });
  }

  shareTicket(id: number, ticketShareForm: TicketShareForm): Observable<any> {
    return this.httpClient.post<any>(
      `${this.baseUrl}/${id}/share`,
      ticketShareForm
    );
  }
}
