import { Observable, Subscription } from 'rxjs';
import { LoungesService } from './../lounges/lounges.service';
import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Lounge } from 'src/models/lounge.model';
import { MediaChange, MediaObserver } from '@angular/flex-layout';
import { filter, map } from 'rxjs/operators';

@Component({
  selector: 'lounge',
  templateUrl: './lounge.component.html',
  styleUrls: ['./lounge.component.scss']
})
export class LoungeComponent implements OnDestroy {
  id: number = parseInt(this.activatedRoute.snapshot.params['id']);
  lounge$: Observable<Lounge>;
  currentDay: string;
  columns = 4;
  watcher: Subscription;

  constructor(
    private loungesService: LoungesService,
    private activatedRoute: ActivatedRoute,
    private media: MediaObserver
  ) {
    this.lounge$ = this.loungesService.getLounge(this.id);
    this.loungesService.getLounge(this.id).subscribe(console.log);
    this.currentDay = this.getCurrentDay();
    this.watcher = media
      .asObservable()
      .pipe(
        filter((changes: MediaChange[]) => changes.length > 0),
        map((changes: MediaChange[]) => changes[0])
      )
      .subscribe((change: MediaChange) => {
        if (change) {
          if (change.mqAlias == 'xs') {
            this.columns = 2;
          } else if (change.mqAlias == 'sm') {
            this.columns = 3;
          } else {
            this.columns = 4;
          }
        }
      });
  }

  ngOnDestroy(): void {
    this.watcher.unsubscribe();
  }

  getCurrentDay(): string {
    const dateObj = new Date();
    return dateObj.toLocaleString('default', { weekday: 'long' }).toUpperCase();
  }
}
