import { CountriesService } from './../tickets/countries.service';
import { SelectObject } from 'src/models/select-object.model';
import { CountryDetails } from '../../models/country-details.model';
import { TicketsService } from './../tickets/tickets.service';
import { Component } from '@angular/core';
import { Observable } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { Lounge } from 'src/models/lounge.model';
import { LoungesService } from '../lounges/lounges.service';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { CustomValidator } from 'src/utilities/validators';
import { map } from 'rxjs/operators';
import { ResponseDialogComponent } from '../response-dialog/response-dialog.component';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'buy-ticket',
  templateUrl: './buy-ticket.component.html',
  styleUrls: ['./buy-ticket.component.scss']
})
export class BuyTicketComponent {
  id: number = parseInt(this.activatedRoute.snapshot.params['id']);
  lounge$: Observable<Lounge>;
  countryDetails: SelectObject[];

  constructor(
    private loungesService: LoungesService,
    private ticketsService: TicketsService,
    private countriesService: CountriesService,
    private fb: FormBuilder,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    public dialog: MatDialog
  ) {
    this.lounge$ = this.loungesService.getLounge(this.id);
    this.getCountries();
  }

  name: FormControl = new FormControl('', [
    CustomValidator.required,
    CustomValidator.lettersOnly
  ]);
  surname: FormControl = new FormControl('', [
    CustomValidator.required,
    CustomValidator.lettersAndSpaces
  ]);
  email: FormControl = new FormControl('', [
    CustomValidator.required,
    CustomValidator.email
  ]);
  phonePrefixLength: FormControl = new FormControl('', [
    CustomValidator.required
  ]);
  phone: FormControl = new FormControl('', [
    CustomValidator.required,
    CustomValidator.numbersOnly,
    CustomValidator.minLength(5),
    CustomValidator.maxLength(12)
  ]);
  adultCount: FormControl = new FormControl('', [
    CustomValidator.required,
    CustomValidator.min(1)
  ]);
  childCount: FormControl = new FormControl('', [CustomValidator.min(0)]);
  expireDate: FormControl = new FormControl('', [
    CustomValidator.required,
    CustomValidator.minDateToday
  ]);

  buyTicketForm: FormGroup = this.fb.group({
    name: this.name,
    surname: this.surname,
    email: this.email,
    phone: this.phone,
    phonePrefixLength: this.phonePrefixLength,
    adultCount: this.adultCount,
    childCount: this.childCount,
    expireDate: this.expireDate
  });

  submitHandler(): void {
    this.buyTicketForm.disable();
    this.ticketsService
      .reserveTicket({
        ...this.buyTicketForm.value,
        phone: this.phonePrefixLength.value + this.phone.value,
        phonePrefixLength: this.phonePrefixLength.value.length,
        loungeId: this.id
      })
      .subscribe(
        (lounge: Lounge) => this.router.navigate(['/tickets/' + lounge.id]),
        (exc) => {
          this.buyTicketForm.enable();
          this.dialog.open(ResponseDialogComponent, {
            width: '450px',
            autoFocus: false,
            data: {
              title: 'Error!',
              description: exc.error
            }
          });
        }
      );
  }

  private getCountries(): void {
    this.countriesService
      .getCountries()
      .pipe(
        map((countryDetails: CountryDetails[]) =>
          Object.values(countryDetails).map((countryDetail) => {
            return {
              name: `${countryDetail.name} (+${countryDetail.phone})`,
              value: '+' + countryDetail.phone
            } as SelectObject;
          })
        )
      )
      .subscribe(
        (countryDetails: SelectObject[]) =>
          (this.countryDetails = countryDetails),
        (exc) =>
          this.dialog.open(ResponseDialogComponent, {
            width: '450px',
            autoFocus: false,
            data: {
              title: 'Error!',
              description: exc.error
            }
          })
      );
  }
}
