import { TicketsService } from './tickets.service';
import { AfterViewInit, Component, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { Ticket } from 'src/models/ticket.model';
import { MatSort } from '@angular/material/sort';
import { map } from 'rxjs/operators';
import { TicketDatabase } from 'src/models/ticket-database.model';
import { TitleCasePipe } from '@angular/common';

@Component({
  selector: 'tickets',
  templateUrl: './tickets.component.html',
  styleUrls: ['./tickets.component.scss']
})
export class TicketsComponent implements AfterViewInit {
  constructor(
    private ticketService: TicketsService,
    public titleStr: TitleCasePipe
  ) {}

  @ViewChild(MatPaginator) paginator: MatPaginator;
  dataSource: MatTableDataSource<TicketDatabase>;

  @ViewChild(MatSort) sort: MatSort;

  displayedColumns: string[] = [
    'created',
    'fullName',
    'userEmail',
    'state',
    'loungeName',
    'guestEntrancesLength',
    'childCount',
    'pnr',
    'options'
  ];

  ngAfterViewInit(): void {
    this.ticketService
      .getTickets()
      .pipe(map((tickets) => this.mapTickets(tickets)))
      .subscribe((tickets: TicketDatabase[]) => {
        this.dataSource = new MatTableDataSource<TicketDatabase>(tickets);
        this.dataSource.sort = this.sort;
        this.dataSource.paginator = this.paginator;
      });
  }

  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  private mapTickets(tickets: Ticket[]): TicketDatabase[] {
    return tickets.map((ticket) => {
      return {
        id: ticket.id,
        created: ticket.created,
        fullName: `${this.titleStr.transform(
          ticket.user.name
        )} ${this.titleStr.transform(ticket.user.surname)}`,
        userEmail: ticket.user.email,
        state: ticket.state,
        loungeName: ticket.lounge.name,
        guestEntrancesLength: ticket.guestEntrances.length + 1,
        childCount: ticket.childCount,
        pnr: ticket.pnr
      } as TicketDatabase;
    });
  }
}
