import { Airport } from './../../models/airport.model';
import { FormControl } from '@angular/forms';
import { Component } from '@angular/core';
import { AirportsService } from './airports.service';
import { debounce, tap } from 'rxjs/operators';
import { Subscription, timer } from 'rxjs';

@Component({
  selector: 'airports',
  templateUrl: './airports.component.html',
  styleUrls: ['./airports.component.scss']
})
export class AirportsComponent {
  searchKey: FormControl = new FormControl('');
  airports: Airport[];
  airportsSub: Subscription;

  constructor(private airportsService: AirportsService) {
    this.searchKey.valueChanges
      .pipe(
        debounce((searchKey: string) => timer(searchKey ? 750 : 0)),
        tap(() => this.airportsSub?.unsubscribe())
      )
      .subscribe((searchKey: string) =>
        searchKey ? this.fetchAirports(searchKey) : (this.airports = [])
      );
  }

  fetchAirports(searchKey: string): void {
    this.airportsSub = this.airportsService
      .getAirports(searchKey)
      .subscribe((airports: Airport[]) => (this.airports = airports));
  }
}
