import { environment } from './../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { switchMap, tap } from 'rxjs/operators';
import { Observable, throwError, of } from 'rxjs';
import { LoginResponse } from 'src/models/login-response.model';
import { Login } from 'src/models/login.model';
import { __TOKEN__ } from 'src/utilities/constants';

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  constructor(private httpClient: HttpClient) {}

  get baseUrl(): string {
    return `${environment.url}/login`;
  }

  login(login: Login): Observable<string> {
    return this.httpClient.post(this.baseUrl, login).pipe(
      switchMap(this.checkLoginResponse),
      tap((token: string) => localStorage.setItem(__TOKEN__, token))
    );
  }

  private checkLoginResponse(res: LoginResponse): Observable<string> {
    return res.type != 'SUCCESS'
      ? throwError('Invalid Email or Password!')
      : of(res.authToken);
  }
}
