import { Airport } from './../../models/airport.model';
import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Lounge } from 'src/models/lounge.model';
import { AirportsService } from './../airports/airports.service';

@Component({
  selector: 'lounges',
  templateUrl: './lounges.component.html',
  styleUrls: ['./lounges.component.scss']
})
export class LoungesComponent {
  lounge: Lounge;
  airport: Airport;
  id: number = parseInt(this.activatedRoute.snapshot.params['id']);

  constructor(
    private airportService: AirportsService,
    private activatedRoute: ActivatedRoute
  ) {
    this.airportService.getAirport(this.id).subscribe((airport: Airport) => {
      this.airport = airport;
      console.log(this.airport);
    });
  }
}
