import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Airport } from 'src/models/airport.model';
import { Lounge } from 'src/models/lounge.model';

@Injectable({
  providedIn: 'root'
})
export class LoungesService {
  constructor(private httpClient: HttpClient) {}

  get baseUrl(): string {
    return `${environment.url}/lounges`;
  }

  getLounge(id: number): Observable<Lounge> {
    return this.httpClient.get<Lounge>(`${this.baseUrl}/${id}`);
  }
}
