import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Airport } from 'src/models/airport.model';

@Injectable({
  providedIn: 'root'
})
export class AirportsService {
  constructor(private httpClient: HttpClient) {}

  get baseUrl(): string {
    return `${environment.url}`;
  }

  getAirports(searchKey: string): Observable<Airport[]> {
    return this.httpClient.get<Airport[]>(
      `${this.baseUrl}/search?searchKey=${searchKey}`
    );
  }

  getAirport(id: number): Observable<Airport> {
    return this.httpClient.get<Airport>(`${this.baseUrl}/airports/${id}`);
  }
}
