import { ConfirmDialogComponent } from '../confirm-dialog/confirm-dialog.component';
import { LanguageDialogComponent } from '../language-dialog/language-dialog.component';
import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Ticket } from 'src/models/ticket.model';
import { filter } from 'rxjs/operators';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { TicketsService } from '../tickets/tickets.service';
import { ResponseDialogComponent } from 'src/components/response-dialog/response-dialog.component';

@Component({
  selector: 'ticket',
  templateUrl: './ticket.component.html',
  styleUrls: ['./ticket.component.scss']
})
export class TicketComponent {
  ticket: Ticket;
  id: number = parseInt(this.activatedRoute.snapshot.params['id']);
  email: FormControl = new FormControl(false);
  phone: FormControl = new FormControl(false);
  shareTicketForm: FormGroup = this.fb.group({
    phone: this.phone,
    email: this.email
  });

  constructor(
    private thicketService: TicketsService,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    public dialog: MatDialog
  ) {
    this.thicketService
      .getTicket(this.id)
      .subscribe((ticket: Ticket) => (this.ticket = ticket));
  }

  submitHandler(): void {
    const closeDialogRef = this.dialog.open(LanguageDialogComponent, {
      width: '450px',
      autoFocus: false
    });

    closeDialogRef
      .afterClosed()
      .pipe(filter((lang: string) => (lang ? true : false)))
      .subscribe((lang: string) => {
        this.shareTicketForm.disable();
        this.thicketService
          .shareTicket(this.id, {
            ...this.shareTicketForm.value,
            lang: lang
          })
          .subscribe(() => {
            this.dialog.open(ResponseDialogComponent, {
              width: '450px',
              autoFocus: false,
              data: {
                title: 'Success!',
                description: 'Ticket shared successfully!'
              }
            });
            this.shareTicketForm.reset();
            this.shareTicketForm.enable();
          });
      });
  }

  cancelTicket(): void {
    const confirmDialogRef = this.dialog.open(ConfirmDialogComponent, {
      width: '450px',
      autoFocus: false,
      data: {
        title: 'Cancel Ticket',
        description: 'Do you really want to cancel the ticket?'
      }
    });

    confirmDialogRef
      .afterClosed()
      .pipe(filter((res: boolean) => res))
      .subscribe(() => {
        this.shareTicketForm.disable();
        this.thicketService.cancelTicket(this.id).subscribe(
          () => {
            this.dialog.open(ResponseDialogComponent, {
              width: '450px',
              autoFocus: false,
              data: {
                title: 'Success!',
                description: 'Ticket cancelled successfully!'
              }
            });
            this.thicketService
              .getTicket(this.id)
              .subscribe((ticket: Ticket) => (this.ticket = ticket));
          },
          (exc) => {
            this.dialog.open(ResponseDialogComponent, {
              width: '450px',
              autoFocus: false,
              data: {
                title: 'Error!',
                description: exc.error
              }
            });
            this.shareTicketForm.enable();
          }
        );
      });
  }
}
