import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { CountryDetails } from 'src/models/country-details.model';

@Injectable({
  providedIn: 'root'
})
export class CountriesService {
  constructor(private httpClient: HttpClient) {}

  get baseUrl(): string {
    return `https://s3.eu-central-1.amazonaws.com/solid.util/countries.json`;
  }

  getCountries(): Observable<CountryDetails[]> {
    return this.httpClient.get<CountryDetails[]>(this.baseUrl);
  }
}
