import { BuyTicketComponent } from 'src/components/buy-ticket/buy-ticket.component';
import { LoungesComponent } from 'src/components/lounges/lounges.component';
import { AirportsComponent } from './../../components/airports/airports.component';
import { LoginGuard } from './../../utilities/login.guard';
import { AuthGuard } from '../../utilities/auth.guard';
import { TicketsComponent } from './../../components/tickets/tickets.component';
import { LoginComponent } from './../../components/login/login.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TicketComponent } from 'src/components/ticket/ticket.component';
import { LoungeComponent } from 'src/components/lounge/lounge.component';

const routes: Routes = [
  { path: '', component: LoginComponent, canActivate: [LoginGuard] },
  {
    path: 'tickets/:id',
    component: TicketComponent,
    canActivate: [AuthGuard]
  },
  { path: 'tickets', component: TicketsComponent, canActivate: [AuthGuard] },
  {
    path: 'airports/:id',
    component: LoungesComponent,
    canActivate: [AuthGuard]
  },
  { path: 'airports', component: AirportsComponent, canActivate: [AuthGuard] },
  {
    path: 'lounges/:id/buy-ticket',
    component: BuyTicketComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'lounges/:id',
    component: LoungeComponent,
    canActivate: [AuthGuard]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
